.wssp-floating{position:fixed;bottom:15px;left:15px;font-size:14px;transition:bottom 0.2s}.wssp-floating .wssp-floating-button{position:relative;border-radius:50%;box-shadow:1px 1px 4px rgba(60,60,60,.4);transition:box-shadow 0.2s;cursor:pointer;overflow:hidden}.wssp-floating .wssp-floating-button img,.wssp-floating .wssp-floating-button svg{position:absolute;width:100%;height:auto;object-fit:cover;top:50%;left:50%;transform:translate3d(-50%,-50%,0);border-radius:50%}.wssp-floating:hover{bottom:17px}.wssp-floating:hover .wssp-floating-button{box-shadow:1px 2px 8px rgba(60,60,60,.4)}.wssp-floating .wssp-floating-popup{border-radius:6px;background-color:#e5ddd5;position:absolute;overflow:hidden;padding:0;box-shadow:1px 2px 8px rgba(60,60,60,.25);width:0;height:0;bottom:0;opacity:0;transition:bottom 0.1s ease-out,opacity 0.2s ease-out;transform-origin:bottom}.wssp-floating .wssp-floating-popup.active{padding:0 12px 12px;width:260px;height:auto;bottom:82px;opacity:1}.wssp-floating .wssp-floating-popup .wssp-floating-message{background-color:#fff;padding:8px;border-radius:0 5px 5px;box-shadow:1px 1px 1px rgba(0,0,0,.15);opacity:0;transition:opacity 0.2s}.wssp-floating .wssp-floating-popup.active .wssp-floating-message{opacity:1;transition-delay:0.2s}.wssp-floating .wssp-floating-popup .wssp-floating-head{text-align:right;color:#fff;margin:0 -15px 10px;padding:6px 12px;display:flex;justify-content:space-between;cursor:pointer}.wssp-floating .wssp-floating-input-message{background-color:#fff;margin:10px -15px -15px;padding:0 15px;display:flex;align-items:center}.wssp-floating .wssp-floating-input-message textarea{border:1px solid #ccc;border-radius:4px;box-shadow:none;padding:8px;margin:10px 0;width:100%;max-width:100%;font-family:inherit;font-size:inherit;resize:none}.wssp-floating .wssp-floating-btn-send{margin-left:12px;font-size:0;cursor:pointer}